import * as Tone from "tone";
import memoize from "lodash.memoize";

const bufferResource = (url) => {
  console.log(url);
  let buffer;
  new Promise((resolve) => {
    buffer = new Tone.Player(url, () => {
      console.log("sample loaded");
      resolve(buffer);
    });
    buffer.autostart = true;
  });
  return buffer;
};

export default memoize(bufferResource);
